import React from 'react'
import Card from './Card';

const Cards = () => {

  const data = [
    {
      "id": "R1-Verificar usuario",
      "como": "Recepcionista",
      "quiero": "Capturar o actualizar los datos del cliente",
      "para": "Comprobar si tiene expediente o crear uno nuevo",
      "condiciones": <ul>
        <li>Se observa el logo del laboratorio.</li>
        <li>Solicita nombre completo y fecha de nacimiento.</li>
        <li>Arroja el usuario registrado en caso que lo haya.</li>
      </ul>,
    },
    {
      "id": "S2-Codigo de cliente nuevo",
      "como": "Cliente",
      "quiero": "Que se me otorgue un codigo de usuario",
      "para": "Consultar mi expediente e historial",
      "condiciones": <ul>
        <li>El codigo es unico y generado mediante un algoritmo</li>
      </ul>,
    },
    {
      "id": "S3-Expediente",
      "como": "Cliente y administrativos",
      "quiero": "Que se alojen mis datos, mi expediente y mi historial en el usuario",
      "para": "Almacenar y consultar toda mi información",
      "condiciones": <ul>
        <li>Se necesita nombre completo y fecha de nacimiento para crear usuario</li>
        <li>Una persona no puede tener mas de un usuario</li>
        <li>Un usuario no se puede repetir</li>
      </ul>,
    }

  ];

    return (
      <>
      {data.map(({id, como, quiero, para, condiciones}) => {
        return (<Card 
        key={id}
        id={id}
        como={como}
        quiero={quiero}
        para={para}
        condiciones={condiciones}
        />
        );
      })}
      </>
    );
};

export default Cards;
