import React from 'react'
import PropTypes from 'prop-types'
import "./Card.css"



const Card = ({id, como, quiero, para, condiciones}) => {
  return (
    <div className="card">
      <div className="card-text">
      <h2>{id}</h2>
      <h3>Como:</h3><p>{como}</p>
      <h3>Quiero: </h3><p>{quiero}</p>
      <h3>Para:</h3><p>{para}</p>
      <h3>Condiciones:</h3><p>{condiciones}</p>
      </div>
    </div>
  );
};

Card.propTypes = {
  id: PropTypes.string,
  como: PropTypes.string,
  quiero: PropTypes.string,
  para: PropTypes.string,
  condiciones: PropTypes.string,
};

export default Card;
